
@import "bootstrap-classes";
@import "global";
@import "sass/commonSections/smart-banner";

/* Start Hero Image */
.homepage-hero {
  background-image: url(../imgs/homepage/homepage-2x.webp);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  background-color: #162932;

  .container {
    position: relative;
    z-index: 2;
  }

  .hero-img-txt {
    color: #ffffff;
    font-size: 32px;
    line-height: 45px;
    font-weight: 500;
    @media #{$betweentabdesk} {
      font-size: 25px;
      line-height: 38px;
    }
    @media #{$tablet} {
      font-size: 22px;
      line-height: 32px;
    }
  }
  .title {
    @media #{$betweentabdesk} {
      font-size: 38px;
    }
    @media #{$tablet} {
      font-size: 30px;
    }
  }
  .hero-img-title {
    font-size: 53px;
    line-height: 60px;
    margin-bottom: 40px;
    margin-top: 0;
    color: #ffffff;
    @media #{$betweentabdesk} {
      font-size: 42px;
      line-height: 50px;
    }
    @media #{$tablet} {
      font-size: 50px;
      line-height: 55px;
      margin-bottom: 30px;
    }
  }

  .trustpilot_img {
    margin-top: 35px;
    @media (min-width: 768px) and (max-width: 1199px) {
      img {
        height: 30px;
        width: auto;
      }
    }
  }
  .hero-img-content {
    .homepage-2-button {
      display: inline-block;
      text-align: center;
      .hero-img-btn {
        line-height: 64px;
        width: 100%;
        padding: 0 20px;
        min-width: 260px;
        @media #{$betweentabdesk} {
          font-size: 16px;
        }
        @media #{$tablet} {
          width: 260px;
          font-size: 16px;
        }
        .arrow-triangle {
          margin-left: 5px;
        }
      }
      p {
        font-weight: 500;
        font-size: 16px;
        line-height: 20px;
        color: #ffffff;
        opacity: 1;
        margin-top: 10px;
        @media #{$betweentabdesk} {
          font-size: 14px;
        }
        @media #{$maxTab} {
          font-size: 14px;
          line-height: 17px;
        }
      }
    }
  }

  @media (min-width: 1000px) and (max-width: 1200px) {
    padding: 120px 0 90px;
  }
}

/* End Hero Image */

// Start Homepage Testimonials
.homepage-testimonials {
  padding: 30px 0;
  background-color: #fff;
  .testimonial-content {
    @media #{$tablet} {
      .left,
      .right {
        padding-right: 0;
      }
    }
  }
  .testimonial {
    display: flex;
    align-items: center;
    @media #{$maxTab} {
      align-items: flex-start;
    }

    &.one {
      position: relative;
      @media (min-width: 768px) and (max-width: 1199px) {
        .text {
          padding-right: 15px;
        }
      }
      &:after {
        content: "";
        width: 0.8px;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;
        background-color: #e9edef;
      }
    }
    .image {
      margin-right: 40px;
      @media #{$maxTab} {
        margin-right: 13px;
        img {
          width: 80px;
          height: auto;
        }
      }
    }
    .text {
      flex: 1;
      .testimonial-text {
        font-weight: 500;
        font-size: 12px;
        line-height: 20px;
        max-width: 400px;
        @media #{$maxTab} {
          font-size: 14px;
          line-height: 140%;
        }
      }
      .author {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 10px;
        position: relative;
        .author-info {
          font-size: 12px;
          line-height: 15px;
          @media #{$maxTab} {
            font-size: 14px;
            line-height: 22px;
          }
          h3 {
            font-weight: 500;
            color: #fd3e72;
            font-size: inherit;
          }
          p {
            color: rgba(91, 88, 99, 0.6);
          }
        }
        .quote {
          position: absolute;
          right: 24px;
          top: 24px;
          font-weight: 500;
          font-size: 100px;
          line-height: 22px;
          color: rgba(253, 62, 114, 0.1);
        }
      }
    }
  }
}
// End Homepage Testimonials

.press-advantages.homepage {
  .feat-icon {
    width: 64px;
    height: 64px;
    margin: auto;
    line-height: 64px;
  }
  .advantage .advantage-text p {
    width: 73%;
    @media #{$tablet} {
      width: 84%;
    }
  }
  .advantage .advantage-text h4 {
    @media #{$tablet} {
      font-size: 16px;
    }
  }
}

.get-started-login {
  p {
    font-size: 14px;
    line-height: 17px;
    color: #5b5863;
    margin-bottom: 0;
    a:first-child {
      transition: all 0.3s ease-in-out;
      font-size: 16px;
      font-weight: 500;
      @media #{$maxTab} {
        font-size: 18px;
      }
      &:hover {
        color: $primaryColor;
      }
    }
    .my-account {
      background: #fefefe;
      border: 1px solid #e3e0d2;
      display: inline-block;
      border-radius: 5px;
      padding: 10px 15px;
      color: #fd3e72;
      font-weight: 500;
      margin-left: 12px;
      transition: all 0.3s ease-in-out;
      @media #{$maxTab} {
        display: block;
        margin-left: 0;
        padding: 13.5px;
        background-color: #fd3e72;
        color: #fff;
        text-align: center;
        svg {
          path {
            fill: #fff;
          }
        }
      }

      svg {
        transform: translate(-2px, -2px);

        path {
          transition: all 0.3s ease-in-out;
        }
      }
      &:hover {
        background-color: #fd3e72;
        color: #fff;
        border-color: #fd3e72;
        svg {
          path {
            fill: #fff;
          }
        }
      }
    }
  }
}

.get-started-login.color-bg-pages {
  p {
    a:first-child {
      color: #fff;
      @media #{$maxTab} {
        color: #5b5863;
        margin-top: 10px;
        display: inline-block;
      }

      &:hover {
        color: $primaryColor;
      }
    }
  }
}

