.unblock-content {
  background: #f4f6f7;
  padding: 100px 0;
  position: relative;
  @media #{$tablet} {
    padding: 80px 0;
  }

  .unblock {
    max-width: 400px;
    margin-left: auto;
    @media #{$maxTab} {
      max-width: none;
    }
    &-title {
      font-size: 40px;
      line-height: 44px;
      @media #{$maxTab} {
        font-size: 32px;
        line-height: 42px;
      }
      .span {
        color: $primaryColor;
      }
    }
    &-text {
      font-size: 22px;
      line-height: 32px;
      max-width: 380px;
      margin: 40px 0;
      @media #{$maxTab} {
        font-size: 18px;
        line-height: 28px;
        max-width: none;
      }
    }
    .get-wevpn-button {
      display: inline-block;
      text-align: center;
      a {
        width: 100%;
        min-width: 260px;
      }
    }
  }
}

.anonymous {
  background-color: #fff;
  padding: 120px 0;
  @media #{$tablet} {
    padding: 80px 0;
  }
  &-title {
    font-size: 40px;
    line-height: 50px;
    @media #{$maxTab} {
      font-size: 32px;
      line-height: 42px;
    }
    .span {
      color: $primaryColor;
    }
  }
  &-desc {
    font-size: 22px;
    line-height: 32px;
    max-width: 390px;
    margin-top: 40px;
    @media #{$maxTab} {
      font-size: 18px;
      line-height: 28px;
      max-width: none;
    }
  }
}

.wevpn-feats {
  background: #f4f6f7;
  padding: 60px 0 75px;
  .feat-icon {
    width: 112px;
    height: 98px;
    margin: auto;
    display: flex;
    img {
      margin: auto;
    }
  }
  .advantage-text {
    position: relative;
    &:after {
      content: "";
      position: absolute;
      top: 0;
      right: -15px;
      height: 100%;
      width: 1px;
      background-color: #ebe8df;
    }
    &.last {
      &:after {
        display: none;
      }
    }
    h4 {
      margin: 40px 0 20px;
      font-size: 20px;
      line-height: 20px;
      @media #{$tablet} {
        margin: 25px 0 10px;
        font-size: 14px;
      }
    }
    p {
      font-size: 16px;
      line-height: 28px;
      width: 90%;
      margin: auto;
      @media #{$maxTab} {
        font-size: 14px;
        line-height: 22px;
        width: 100%;
      }
    }
  }
}

.wevpn_communities {
  position: relative;
  .content {
    position: absolute;
    top: -51px;
    left: 15px;
    right: 15px;
    z-index: 1;
    @media #{$tablet} {
      top: -85px;
    }
  }
  .widget {
    border-radius: 10px;
    display: flex;
    @media #{$tablet} {
      flex-direction: column;
      text-align: center;
    }
    &.twitter {
      background: #16addf;
      .widget__text {
        background: #33ccff;
      }
      .widget__link {
        a:hover {
          box-shadow: 0px 6px 9px darken(#16addf, 5);
        }
      }
    }
    &.discord {
      background: #5870c5;
      .widget__text {
        background: #7289da;
      }
      .widget__link {
        a:hover {
          box-shadow: 0px 6px 9px darken(#5870c5, 5);
        }
      }
    }
    &.reddit {
      background: #e94204;
      .widget__text {
        background: #ff4500;
      }
      .widget__link {
        a:hover {
          box-shadow: 0px 6px 9px darken(#e94204, 5);
        }
      }
    }
    &__text {
      flex-basis: 51%;
      border-radius: 10px 0px 30px 10px;
      padding: 29px 18px;
      color: #f9f8f4;
      font-weight: 500;
      font-size: 16px;
      line-height: 140%;
      @media #{$tablet} {
        border-radius: 10px 10px 20px 20px;
        padding: 20px 30px;
      }
    }
    &__link {
      flex-basis: 49%;
      display: flex;
      align-items: center;
      justify-content: center;
      @media #{$tablet} {
        padding: 17px;
      }
      a {
        transition: all 0.3s ease-in-out;
        width: 134px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        background-color: #fff;
        border-radius: 5px;
      }
    }
  }
}

