    .text {
      font-size: 16px;
      line-height: 26px;
      max-width: 420px;
      color: #5b5863;
      @media #{$maxTab} {
        font-size: 14px;
        line-height: 26px;
      }
    }
  }
}

.home-trustbox {
  background: #fafafa;
  padding: 50px 0;
}

// Start Wevpn Features
.wevpn-features {
  background-color: #fff;
  position: relative;
  padding-top: 90px;
  overflow: hidden;
  @media #{$maxTab} {
    padding-top: 0;
  }
  &:after {
    content: "";
    position: absolute;
    right: 0;
    width: 50%;
    top: 0;
    bottom: 0;
    background: linear-gradient(154.48deg, #fe6556 -9.73%, #fd3e72 66.22%, #49a8ff 163.3%);
    z-index: 0;
  }
  .wevpn_features_content {
    max-width: 1200px;
    margin: auto;
    padding: 0 15px;
  }
  .row {
    > div {
      display: flex;
      padding: 0;
    }
  }
  .support {
    position: relative;
    z-index: 1;
    width: 100%;
    top: 1px;

    @media (min-width: 1050px) {
      max-width: 475px;
      margin: 0 auto;
    }
    @media (max-width: 1050px) {
      padding: 0 30px;
    }
    @media #{$tablet} {
      max-width: 350px;
      margin: 0 auto;
    }
    h3 {
      @media (min-width: 992px) {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 80px;
      }
      @media #{$maxTab} {
        padding-top: 65px;
      }
    }
    .text {
      margin: 30px auto 50px;
      font-size: 18px;
      line-height: 160%;
      @media #{$maxTab} {
        margin: 20px auto 30px;
      }
    }
    .support-btn {
      margin-bottom: 53px;
      display: inline-block;
      text-align: center;
      a {
        width: 100%;
        min-width: 263px;
        line-height: 64px;
      }
      p {
        margin-top: 10px;
        font-weight: 500;
        @media #{$maxTab} {
          font-size: 13px;
        }
      }
    }
    .image.man-img {
      img {
        @media #{$maxTab} {
          max-height: 225px;
          width: 154px;
        }
      }
    }
  }
  .money-back {
    color: #fff;
    .support-btn {
      a {
        &.primary__btn {
          background-color: #fff;
          color: $primaryColor;
          svg {
            transition: all 0.3s ease-in-out;
            margin-left: 5px;
          }
          &:hover {
            svg {
              transform: translateX(3px);
            }
            background-color: #ffecf1;
          }
        }
      }
    }
  }
}
