@import "sass/helpers/variables";
@font-face {
  font-family: "Averta";
  src: url("../fonts/Averta-Regular/Averta-Regular.woff") format("woff"), url("../fonts/Averta-Regular/Averta-Regular.eot") format("opentype"), url("../fonts/Averta-Regular/Averta-Regular.ttf") format("truetype"), url("../fonts/Averta-Regular/Averta-Regular.svg#Averta-Regular") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Averta";
  src: url("../fonts/Averta-Semibold/Averta-Semibold.woff") format("woff"), url("../fonts/Averta-Semibold/Averta-Semibold.eot") format("opentype"), url("../fonts/Averta-Semibold/Averta-Semibold.ttf") format("truetype"), url("../fonts/Averta-Semibold/Averta-Semibold.svg#Averta-Semibold") format("svg");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Averta";
  src: url("../fonts/Averta-Bold/Averta-Bold.woff") format("woff"), url("../fonts/Averta-Bold/Averta-Bold.eot") format("opentype"), url("../fonts/Averta-Bold/Averta-Bold.ttf") format("truetype"), url("../fonts/Averta-Bold/Averta-Bold.svg#Averta-Bold") format("svg");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

input,
textarea,
select {
  color: #5b5863 !important;
  box-shadow: none !important;
  outline: none !important;
  height: auto;

  &.form-control {
    height: auto;
  }

  ::placeholder {
    color: #8d8b92;
  }

  &:focus {
    &::-webkit-input-placeholder {
      opacity: 0.5;
      transition: opacity 0.3s ease-in-out;
      box-shadow: none;
      outline: none;
    }
  }
}

.referral-bar {
  background-color: $primaryColor;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 8px 0;
  position: relative;
  z-index: 2;
  display: none;
  p {
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    color: #fafafa;
    margin: 0 10px;
  }
}

