input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.wevpn-link {
  color: $primaryColor;
  text-decoration: underline !important;
  transition: all 0.3s ease-in-out;
  font-weight: 500;

  i,
  svg {
    font-weight: bold;
    margin-left: 5px;
    transition: all 0.3s ease-in-out;
  }

  &:hover {
    color: $linkHoverColor !important;
    text-decoration: none !important;

    i,
    svg {
      transform: translateX(3px);
    }
  }
}

.mainColor {
  color: $primaryColor;
}

.copy-ip {
  background: #fd3e72;
  border-radius: 5px;
  cursor: pointer;
  position: relative;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  display: inline-block;
  &:hover {
    .tooltiptext {
      visibility: visible;
      opacity: 1;
    }
  }
  .tooltiptext {
    visibility: hidden;
    background-color: #4f4f4f;
    font-size: 12px;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    padding: 3px 11px;
    position: absolute;
    z-index: 1;
    bottom: 115%;
    left: 50%;
    transform: translatex(-50%);
    opacity: 0;
    transition: opacity 0.3s;
    font-weight: 500;
    line-height: 24px;
    @media #{$mobile} {
      font-size: 12px;
      padding: 4px 8px;
    }

    &.changed {
      background-color: #08ae7e;
      &:after {
        border-color: #08ae7e transparent transparent transparent;
      }
    }
    &:after {
      content: "";
      position: absolute;
      top: 100%;
      left: 50%;
      margin-left: -5px;
      border-width: 5px;
      border-style: solid;
      border-color: #4f4f4f transparent transparent transparent;
    }
  }
}

.main-bg {
  background-color: #f9f8f4;
}

select {
  background: url(../imgs/Arrow.png) no-repeat right;
  -webkit-appearance: none;

  &:invalid {
    color: #8d8b92;
  }

  & option:first-child {
    color: #8d8b92;
  }

  &:invalid option:not(:first-child) {
    color: #5b5863;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0;
  /* <-- Apparently some margin are still there even though it's hidden */
}

.bull {
  color: #c1c0c4;
}

input[type="number"] {
  -moz-appearance: textfield;
  /* Firefox */
}

body {
  font-family: "Averta", "Segoe UI", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-style: normal;
  position: relative;
  overflow-x: hidden;
  color: #5b5863;
  background-color: #fafafa;

  &.body-dashboard {
    background-color: #f2f2ed;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0;
  font-weight: bold;
}

p {
  margin-bottom: 0;
}

::selection {
  background-color: #5b5863;
  color: #fff;
}

a {
  color: #5b5863;
  text-decoration: none !important;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
    position: relative;
  }
}

@media #{$mobile} {
  .container {
    padding: 0 20px !important;
    position: relative;
  }
}

.wrapper {
  overflow: hidden;
}

/* Change the white to any color */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  box-shadow: 0 0 0 30px white inset !important;
}

/* Change text in autofill textbox */
input:-webkit-autofill {
  -webkit-text-fill-color: #5b5863 !important;
}

.wrapper-header {
  overflow: hidden;
  padding-bottom: 150px;
  position: relative;
  @media #{$betweentabdesk} {
    padding-bottom: 100px;
  }
  @media #{$tablet} {
    padding-bottom: 70px;
  }
  &.is-visible {
    overflow: visible;
  }

  @media #{$mobile} {
    padding-bottom: 0;
  }
  &.homepage {
    padding-bottom: 0;
  }
}

.heading-medium {
  font-size: 36px;
  line-height: 44px;

  @media #{$tablet} {
    font-size: 30px;
    line-height: 38px;
  }
  @media #{$mobile} {
    font-size: 24px;
    line-height: 28px;
  }
}

button:focus {
  box-shadow: none;
  outline: none;
}

body.overlay {
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}

body.overlay:after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(#000, 0.5);
  z-index: 1;
  margin: 0;
  overflow: hidden;
}

.arrow {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 13px;
  height: 21px;
  display: inline-block;

  span {
    top: 10px;
    position: absolute;
    width: 8px;
    height: 2px;
    background-color: #acaaae;
    display: inline-block;
    transition: all 0.4s ease;

    &:first-of-type {
      left: 0;
      transform: rotate(45deg);
    }

    &:last-of-type {
      right: 0;
      transform: rotate(-45deg);
    }
  }
}
// Typography
