.h1 {
  font-size: 47px;
  line-height: 140%;
  font-weight: bold;
  @media #{$tablet} {
    font-size: 40px;
  }
  @media #{$mobile} {
    font-size: 28px;
  }
}
h2,
.h2 {
  font-size: 38px;
  line-height: 140%;
  font-weight: bold;
  @media #{$tablet} {
    font-size: 30px;
  }
  @media #{$mobile} {
    font-size: 24px;
  }
}

.h-32 {
  font-size: 32px;
  line-height: 140%;
  font-weight: bold;
  @media #{$tablet} {
    font-size: 28px;
  }
  @media #{$mobile} {
    font-size: 24px;
  }
}

h3 {
  font-size: 24px;
  line-height: 140%;
  @media #{$maxTab} {
    font-size: 20px;
  }
}
h4 {
  font-size: 20px;
  line-height: 140%;
  font-weight: 500;
  @media #{$mobile} {
    font-size: 14px;
  }
}
h5 {
  font-size: 16px;
  line-height: 140%;
  font-weight: 500;
}
h6 {
  font-size: 14px;
  line-height: 140%;
  font-weight: 500;
}

.p-hero {
  font-size: 20px;
  line-height: 160%;
  @media #{$tablet} {
    font-size: 18px;
  }
  @media #{$mobile} {
    font-size: 16px;
  }
}

.p-lg {
