  line-height: 160%;
  @media #{$tablet} {
    font-size: 16px;
  }
  @media #{$mobile} {
    font-size: 14px;
  }
}
.p-md {
  font-size: 16px;
  line-height: 160%;
  @media #{$mobile} {
    font-size: 14px;
  }
}
.p-sm {
  font-size: 14px;
  line-height: 160%;
  @media #{$mobile} {
    font-size: 12px;
  }
}

.p-xs {
  font-size: 12px;
  line-height: 160%;
}

.fw-500 {
  font-weight: 500;
}

.p-100 {
  padding: 100px 0 !important;
  @media #{$maxTab} {
    padding: 60px 0 !important;
  }
}
.p-80 {
  padding: 80px 0 !important;
  @media #{$maxTab} {
    padding: 60px 0 !important;
  }
}

.p-60 {
  padding: 60px 0 !important;
}

.box-shadow {
  background: #ffffff;
  box-shadow: 0px 4px 41px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.opacity-8 {
  opacity: 0.8;
}

img {
  image-rendering: -webkit-optimize-contrast;
  transform: translateZ(0);
  will-change: transform;
}

svg.top-4 {
  transform: translateY(4px);
  @media #{$mobile} {
    transform: translateY(0);
  }
}
.main-btn {
  width: 100%;
  line-height: 64px;
  padding: 0 30px;
}

.lb-title {
  position: relative;
  padding-left: 25px;
  @media #{$maxTab} {
    padding-left: 20px;
    margin-bottom: 20px;
  }
  @media #{$betweentabdesk} {
    padding-left: 20px;
  }
  &::before {
    position: absolute;
    content: "";
    width: 6px;
    height: 100%;
    background-color: $primaryColor;
    left: 0;
    @media #{$maxTab} {
      width: 4px;
    }
  }
}

.m-unset {
  margin: unset;
}

.hero-section {
  padding: 150px 0 100px;
  @media #{$tablet} {
    padding: 60px 0;
  }
  @media #{$mobile} {
    padding: 50px 0 60px;
  }
  @media (min-width: 1200px) and (min-height: 601px) {
    height: calc(100vh - 32px);
    display: flex;
    align-items: center;
  }
  @media (min-height: 1000px) {
    height: auto;
    padding: 150px 0 135px;
  }
  @media (max-height: 600px) {
    height: auto;
    padding: 150px 0 135px;
  }
}

// zd widget
.widget-container.normal {
  border: 0 !important;
}

.text-underline {
  text-decoration: underline !important;
}

.primary__btn,
.secondary__btn {
  background-color: $primaryColor;
  font-weight: 500;
  font-size: 20px;
  text-align: center;
  color: #fff;
  display: inline-block;
  border: 0;
  transition: all 0.3s ease-in-out;
  border-radius: 5px;
  @media #{$tablet} {
    font-size: 18px;
  }
  @media #{$mobile} {
    font-size: 16px;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    line-height: 60px;
  }

  &:hover {
    background-color: $linkHoverColor;
    color: #fff;

    i {
      transform: translateX(3px);
    }
    .arrow-triangle {
      transform: translateX(3px);
    }
  }

  &:active {
    background-color: #ff5785;
  }

  i {
    position: relative;
    transition: transform 0.3s ease-in-out;
    top: 2px;
    margin-left: 10px;
  }
  .arrow-triangle {
    position: relative;
    transition: all 0.3s ease-in-out;
    margin-left: 5px;
    margin-right: unset;
    @media #{$mobile} {
      width: 8px;
      height: 11.2px;
    }

    path {
      transition: all 0.3s ease-in-out;
    }
  }
}

.secondary__btn {
  background-color: #ffecf1;
  color: $primaryColor;

  &:hover {
    background-color: $primaryColor;
    color: #fff;

    svg {
      path {
        fill: white;
      }
    }
  }
}

.white_btn {
  background: #FFFFFF;
  border-radius: 5px;
  font-weight: 500;
  font-size: 20px;
  line-height: 25px;
  color: $primaryColor;
  padding: 26px 19px;
  display: inline-block;
  text-align: center;
  transition: all 0.3s ease-in-out;
  @media #{$mobile} {
    font-size: 16px;
  }
  svg {
    position: relative;
    transition: all 0.3s ease-in-out;
    margin-left: 5px;
    @media #{$mobile} {
      width: 8px;
      height: 11.2px;
    }
  }
  &:hover {
    background: #EBEBEB;
    color: $primaryColor;
    svg {
      transform: translateX(3px);
    }
  }
}


// header
/* Start Header */
.top-header {
  padding: 8px 0;
  background-color: #fff;
  border-bottom: 1px solid #ebeae6;
  position: relative;
  z-index: 10;
  &.dark {
    border: 0;
    background-color: rgba(26, 40, 48, 0.95);
    .top-header-content ul li span {
      &#location,
      &#ip,
      &#isp {
        color: #fff;
      }
    }
  }
  ul {
    li {
      font-size: 13px;
      line-height: 16px;
      color: #a29cb0;
      @media #{$mobile} {
        font-size: 12px;
      }
      .bull {
        color: #a29cb0;
        margin: 0 10px;
        @media #{$mobile} {
          margin: 0 4px;
        }
      }
    }
  }
  a {
    text-decoration-line: underline !important;
    color: $primaryColor;
    font-weight: 500;
    &:hover {
      text-decoration: none !important;
      svg {
        transform: translateX(6px);
      }
    }
    svg {
      transform: translateX(4px);
      transition: all 0.2s ease;
    }
  }
}

.h-m-w {
  @media #{$maxTab} {
    height: 63px;
  }
}

header {
  &.header-white {
    .logo-colored {
      display: none;
    }
    .logo-white {
      display: inline-block;
    }
    .navbar-nav .nav-link {
      color: #fff;
      @media #{$maxTab} {
        color: #5b5863;
      }
    }
    .navbar .dropdown {
      #languageButton {
        color: #fff;
      }
    }
    .navbar-brand {
      @media #{$maxTab} {
        .logo-colored {
          display: inline-block;
        }
        .logo-white {
          display: none;
        }
      }
    }
    .navbar {
      @media #{$maxTab} {
        background-color: #fff;
      }
      .login {
        .login-item {
          .login-link {
            color: #fff;
            &:hover {
              opacity: 0.8;
              color: #fff;
            }
            @media #{$maxTab} {
              color: #5b5863;
              font-size: 18px;
              padding-left: 0;
              display: block;
            }
          }

          .get-started {
            @media (min-width: 992px) {
              background-color: #fff;
              color: $primaryColor;
              &:hover {
                background: #fff;
              }
            }
          }
        }
      }
    }
    #nav-icon {
      background-color: #fff;
    }
    .navbar-light .navbar-nav .nav-link:hover {
      color: #fff !important;
      opacity: 0.8;
      @media #{$maxTab} {
        color: #5b5863 !important;
        opacity: 1;
      }
    }
  }
  .navbar {
    padding: 13px 0;
    position: absolute;
    width: 100%;
    z-index: 5;
    display: block;
    border-bottom: 1px solid transparent;
    .container {
      position: relative;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
      width: 100%;
    }

    &.nav-fixed {
      position: fixed;
      top: 0;
      background-color: #fff;
      border-bottom: 1px solid #ebe8df;
      animation: q 1s cubic-bezier(0.23, 1, 0.32, 1) both;

      @keyframes q {
        0% {
          transform: translateY(-100%);
        }
        100% {
          transform: translateY(0);
        }
      }
      .login .login-item .login-link,
      .navbar-nav .nav-link,
      .lang-button {
        color: #5b5863 !important;
        &:hover {
          color: $primaryColor !important;
          opacity: 1 !important;
        }
      }
      .login .login-item .get-started {
        background-color: $primaryColor !important;
        color: #fff !important;
        &:hover {
          background-color: $linkHoverColor !important;
        }
      }

      .navbar-brand img.logo-white {
        display: none !important;
      }
      .navbar-brand img.logo-colored {
        display: block !important;
      }

      .get-started-login.color-bg-pages {
        p a:first-child {
          color: #5b5863;
          @media #{$maxTab} {
            padding: 12px 14px 12px 0;
            display: inline-block;
          }
        }
      }

      #nav-icon span {
        background-color: $primaryColor;
      }

      .navbar-nav .active > .nav-link {
        color: $primaryColor !important;
      }

      .container:after {
        display: none;
      }
    }

    .get-started-login.color-bg-pages {
      p {
        a:first-child {
          color: #fff;
          @media #{$maxTab} {
            color: #5b5863;
          }
          &:hover {
            color: #fff;
            opacity: 0.8;
          }
        }
      }
    }

    .get-started-login {
      display: none;
      p {
        display: flex;
        align-items: center;
        font-size: 14px;
        line-height: 17px;
        color: #5b5863;
        a:first-child {
          transition: all 0.2s ease-in-out;
          font-weight: 500;
          margin: 0 15px;
          @media #{$maxTab} {
            font-size: 18px;
            padding: 10px 0px;
            margin: 0;
          }
          &:hover {
            color: $primaryColor;
          }
        }
        .my-account {
          background: #fff;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 5px;
          padding: 10px;
          color: #fd3e72;
          font-weight: 500;
          transition: all 0.2s ease-in-out;
          @media #{$maxTab} {
            position: absolute;
            bottom: 0;
            left: 15px;
            right: 15px;
            z-index: 2;
            box-shadow: 0 -1px 0 rgba(34, 46, 58, 0.1);
            padding: 13.5px;
            background-color: #fd3e72;
            color: #fff;
            svg {
              path {
                fill: #fff;
              }
            }
          }

          svg {
            margin-right: 5px;

            path {
              transition: all 0.3s ease-in-out;
            }
          }
          &:hover {
            background-color: #fd3e72;
            color: #fff;
            border-color: #fd3e72;
            svg {
              path {
                fill: #fff;
              }
            }
          }
        }
      }
    }

    .nav-item {
      &.dropdown {
        > a:hover {
          cursor: text;
        }
      }
      li {
        .badge {
          text-transform: uppercase;
          font-size: 10px;
          padding: 0 7px;
          border-radius: 9px 9px 9px 0px;
          line-height: 16px;
          margin-left: 5px;
          color: #fff;
          &-danger {
            background-color: $primaryColor;
          }
        }
      }
      @media (min-width: 992px) {
        &.dropdown {
          &:hover {
            .dropdown--menu {
              opacity: 1;
              visibility: visible;
              transform: translateY(0);
            }
          }

          .dropdown--menu {
            position: absolute;
            border-radius: 5px;
            opacity: 0;
            visibility: hidden;
            transition: opacity, transform 0.7s cubic-bezier(0.4, 0, 0.2, 1);
            background: #ffffff;
