      @media (min-width: 992px) {
        background: #ffffff;
        box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        top: 100%;
        min-width: 170px;
        position: absolute;
        z-index: 1000;
        opacity: 0;
        visibility: hidden;
        transform: translateY(10px);
        transition: opacity, transform 0.7s cubic-bezier(0.4, 0, 0.2, 1);
        left: 5px;
      }
      &::before {
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
        border-bottom: 12px solid #fff;
        top: -8px;
        left: 30px;
        content: "";
