        .login-link {
          font-size: 14px;
          color: inherit;
          padding: 10px;
          font-weight: 500;
          transition: color 0.2s ease-in-out;
          @media #{$betweentabdesk} {
            font-size: 13px;
          }
          @media #{$maxTab} {
            color: #5b5863;
            font-size: 18px;
          }
          &:hover {
            color: $primaryColor;
          }
        }

        .get-started {
          padding: 10px 15px;
          font-size: 14px;
          display: block;
          text-align: center;
          transition: color 0.2s ease-in-out;
          background-color: $primaryColor;
          color: #fff;
          font-weight: 500;
          border-radius: 3px;
          @media #{$betweentabdesk} {
            font-size: 13px;
            padding: 10px;
          }
          @media #{$maxTab} {
            padding: 13.5px;
            font-size: 16px;
          }
          &:hover {
            background-color: $linkHoverColor;
          }
        }
      }
    }
  }

  .navbar-expand-lg .navbar-collapse {
    flex-grow: 1;
    align-items: center;
    flex-basis: 100%;
    @media (min-width: 992px) {
      display: flex !important;
      flex-basis: auto;
    }
  }
  .navbar-nav {
    @media (min-width: 992px) {
      display: flex;
      flex-direction: row;
    }
  }
}

/* Start navbar toggler */
#lang-icon,
#nav-icon {
  border: 1px solid #e1e0de;
  border-radius: 3px;
  width: 36px;
  height: 36px;
  position: relative;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  span {
    margin: auto;
  }
}

#nav-icon span {
  position: absolute;
  height: 2px;
  background: #ef4273;
  border-radius: 34px;
  transform: rotate(-45deg);
  transition: all 0.2s ease-in-out;
}

/* Icons */
#nav-icon span:nth-child(1) {
  top: 12px;
  left: 14px;
  width: 8px;
  transform-origin: left top;
}

#nav-icon span:nth-child(2) {
  top: 20px;
  left: 11px;
  width: 16px;
  transform-origin: 10% center;
}

#nav-icon span:nth-child(3) {
  top: 25px;
  left: 13px;
  transform-origin: left top;
  width: 8px;
}

#nav-icon.open span:nth-child(1) {
  transform: rotate(45deg);
  top: 15px;
  left: 10px;
  width: 16px;
  transform-origin: 40% center;
}

#nav-icon.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

#nav-icon.open span:nth-child(3) {
  transform-origin: -2% center;
  top: 22px;
  left: 12px;
  width: 16px;
}
/* End navbar toggler */
.navbar-light .navbar-nav .nav-link {
  font-weight: 500;
  color: #5b5863;
  font-size: 14px;
  padding: 12px;
  display: block;
  transition: all 0.2s ease-in-out;
  @media #{$betweentabdesk} {
    padding: 12px 5px;
    font-size: 13px;
  }
  @media #{$maxTab} {
    line-height: 25px;
    padding-left: 0;
    font-size: 18px;
  }
}

.navbar-light .navbar-nav .nav-link:hover {
  color: $primaryColor !important;
}

.dropdown-toggle {
  &::after {
    display: inline-block;
    margin-left: 5px;
    vertical-align: middle;
    content: "";
    border-top: 0.3em solid;
    border-right: 0.3em solid transparent;
    border-bottom: 0;
    border-left: 0.3em solid transparent;
    transition: all 0.2s ease;
  }
  &:hover {
    &::after {
      transform: rotate(180deg);
    }
  }
}
/* End Header */



